'use client';

import { motion } from "framer-motion";
import { ExternalLink, Github } from "lucide-react";
import Image from "next/image";
import Link from "next/link";
import { useEffect, useState } from "react";

interface Project {
    id: number;
    title: string;
    description: string;
    imageUrl?: string | null;
    tags: string[];
    demoUrl?: string | null;
    githubUrl?: string | null;
}

export function Projects() {
    const [projects, setProjects] = useState<Project[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const fetchProjects = async () => {
            try {
                const response = await fetch('http://localhost:3001/projects');
                if (response.ok) {
                    const data = await response.json();
                    setProjects(data);
                }
            } catch (error) {
                console.error("Failed to fetch projects:", error);
            } finally {
                setIsLoading(false);
            }
        };

        fetchProjects();
    }, []);

    return (
        <section id="projects" className="py-20 bg-background">
            <div className="container mx-auto px-4 md:px-6">
                <div className="text-center mb-16">
                    <h2 className="text-3xl md:text-4xl font-bold tracking-tighter mb-4">Featured Projects</h2>
                    <p className="text-muted-foreground text-lg max-w-[600px] mx-auto">
                        Here are some of the projects I've worked on. Each one was a unique challenge that helped me grow as a developer.
                    </p>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {isLoading ? (
                        <div className="col-span-full text-center py-10">Loading projects...</div>
                    ) : projects.length === 0 ? (
                        <div className="col-span-full text-center py-10 text-muted-foreground">No projects found.</div>
                    ) : (
                        projects.map((project, index) => (
                            <motion.div
                                key={project.id}
                                initial={{ opacity: 0, y: 20 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                transition={{ duration: 0.5, delay: index * 0.1 }}
                                viewport={{ once: true }}
                            >
                                <Card className="h-full flex flex-col overflow-hidden hover:shadow-lg transition-shadow duration-300">
                                    <div className="relative h-48 w-full bg-muted">
                                        {/* Placeholder for project image */}
                                        <div className="w-full h-full flex items-center justify-center text-muted-foreground">
                                            {project.title} Image
                                        </div>
                                    </div>
                                    <CardHeader>
                                        <CardTitle>{project.title}</CardTitle>
                                    </CardHeader>
                                    <CardContent className="flex-grow">
                                        <p className="text-muted-foreground mb-4">{project.description}</p>
                                        <div className="flex flex-wrap gap-2">
                                            {project.tags.map((tag) => (
                                                <Badge key={tag} variant="secondary">
                                                    {tag}
                                                </Badge>
                                            ))}
                                        </div>
                                    </CardContent>
                                    <CardFooter className="flex justify-between border-t p-6">
                                        <Link
                                            href={project.githubUrl || "#"}
                                            className="inline-flex items-center text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
                                        >
                                            <Github className="w-4 h-4 mr-2" />
                                            Code
                                        </Link>
                                        <Link
                                            href={project.demoUrl || "#"}
                                            className="inline-flex items-center text-sm font-medium text-primary hover:underline underline-offset-4"
                                        >
                                            Live Demo
                                            <ExternalLink className="w-4 h-4 ml-2" />
                                        </Link>
                                    </CardFooter>
                                </Card>
                            </motion.div>
                        ))
                    )}
                </div>
            </div>
        </section>
    );
}
