'use client';

import { motion } from "framer-motion";
import { ArrowRight, Download } from "lucide-react";
import Link from "next/link";
import Image from "next/image";

export function Hero() {
    return (
        <section className="min-h-[calc(100vh-4rem)] flex items-center justify-center py-20 relative overflow-hidden bg-background">
            {/* Background Gradient */}
            <div className="absolute inset-0 z-0">
                <div className="absolute top-0 left-1/4 w-96 h-96 bg-primary/10 rounded-full blur-3xl opacity-50 animate-blob" />
                <div className="absolute bottom-0 right-1/4 w-96 h-96 bg-secondary/10 rounded-full blur-3xl opacity-50 animate-blob animation-delay-2000" />
            </div>

            <div className="container mx-auto px-4 md:px-6 relative z-10 grid md:grid-cols-2 gap-12 items-center">
                {/* Text Content */}
                <motion.div
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    <div className="inline-block rounded-lg bg-muted px-3 py-1 text-sm mb-6">
                        Available for freelance work
                    </div>
                    <h1 className="text-4xl md:text-6xl font-bold tracking-tighter mb-6">
                        Hi, I'm <span className="text-primary">John Doe</span>
                        <br />
                        Full Stack Developer
                    </h1>
                    <p className="text-xl text-muted-foreground mb-8 max-w-[600px]">
                        I build scalable, secure, and beautiful web applications using modern technologies like Next.js, NestJS, and PostgreSQL.
                    </p>
                    <div className="flex flex-col sm:flex-row gap-4">
                        <Link
                            href="#projects"
                            className="inline-flex items-center justify-center rounded-md bg-primary px-8 py-3 text-sm font-medium text-primary-foreground shadow transition-colors hover:bg-primary/90 focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring"
                        >
                            View Projects
                            <ArrowRight className="ml-2 w-4 h-4" />
                        </Link>
                        <Link
                            href="/resume.pdf"
                            target="_blank"
                            className="inline-flex items-center justify-center rounded-md border border-input bg-background px-8 py-3 text-sm font-medium shadow-sm transition-colors hover:bg-accent hover:text-accent-foreground focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring"
                        >
                            Download CV
                            <Download className="ml-2 w-4 h-4" />
                        </Link>
                    </div>
                </motion.div>

                {/* Image Content */}
                <motion.div
                    initial={{ opacity: 0, scale: 0.8 }}
                    animate={{ opacity: 1, scale: 1 }}
                    transition={{ duration: 0.5, delay: 0.2 }}
                    className="relative mx-auto w-full max-w-[500px] aspect-square"
                >
                    <div className="w-full h-full rounded-full border-8 border-muted overflow-hidden relative shadow-2xl">
                        {/* Placeholder for user image. 
                 Using a gray placeholder for now. 
                 Ideally, this would be a real image from the user. 
             */}
                        <div className="w-full h-full bg-muted flex items-center justify-center text-muted-foreground">
                            User Photo
                        </div>
                    </div>

                    {/* Decorative Elements */}
                    <div className="absolute -z-10 top-0 right-0 w-full h-full bg-gradient-to-tr from-primary/20 to-secondary/20 rounded-full blur-2xl transform scale-110" />
                </motion.div>
            </div>
        </section>
    );
}
