'use client';

import { motion } from "framer-motion";

export function About() {
    const skills = [
        "JavaScript (ES6+)", "TypeScript", "React", "Next.js", "Node.js", "NestJS",
        "PostgreSQL", "HTML5 & CSS3", "Tailwind CSS", "Git"
    ];

    return (
        <section id="about" className="py-20 bg-muted/50">
            <div className="container mx-auto px-4 md:px-6">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                    viewport={{ once: true }}
                    className="max-w-3xl mx-auto text-center"
                >
                    <h2 className="text-3xl md:text-4xl font-bold tracking-tighter mb-8">About Me</h2>

                    <p className="text-lg text-muted-foreground mb-8 leading-relaxed">
                        I am a passionate Full Stack Developer with experience in building web applications
                        spanning across various domains. I love solving complex problems and creating efficient,
                        scalable, and user-friendly solutions. My journey in tech has been driven by a constant
                        desire to learn and innovate.
                    </p>

                    <p className="text-lg text-muted-foreground mb-12 leading-relaxed">
                        When I'm not coding, I enjoy exploring new technologies, contributing to open-source
                        projects, and sharing my knowledge with the community.
                    </p>

                    <div className="bg-background rounded-lg p-8 shadow-sm border border-border/50">
                        <h3 className="text-xl font-semibold mb-6">Technical Skills</h3>
                        <div className="flex flex-wrap justify-center gap-2">
                            {skills.map((skill, index) => (
                                <motion.span
                                    key={skill}
                                    initial={{ opacity: 0, scale: 0.8 }}
                                    whileInView={{ opacity: 1, scale: 1 }}
                                    transition={{ duration: 0.3, delay: index * 0.05 }}
                                    viewport={{ once: true }}
                                    className="px-4 py-2 bg-secondary text-secondary-foreground rounded-full text-sm font-medium hover:bg-primary hover:text-primary-foreground transition-colors cursor-default"
                                >
                                    {skill}
                                </motion.span>
                            ))}
                        </div>
                    </div>
                </motion.div>
            </div>
        </section>
    );
}
